declare module Portefeuille.Modeles.Rest.PortefeuillCorporateV1.statPortefeuille {
    export interface IStatPortefeuilleParametres {
        /**
         * Code Banque sous lequel l'tablissement est rpertori par la Banque de France.
         */
        codeEtablissement: string;
        /**
         * Numro d'identification de l'lment de structure Suivi Par.
         */
        identifiantEds: number;
        /**
         * Liste numro de portefeuille.
         */
        listeIdentifiantPortefeuille: Array<number>;
    }

    export interface IPortefeuilleRdv {
        /**
         * Nombre de clients total (non prospects).
         */
        nombreClients: number;
        /**
         * Nombre de cliens actifs.
         */
        nombreClientsActifs: number;
        /**
         * Nombre de prospects.
         */
        nombreProspects: number;
        /**
         * Nombre de relations conomiques (toutes catgories confondues).
         */
        nombreRelationsEconomiques: number;
        /**
         * Nombre de clients de type Entreprises - ESI.
         */
        nombreClientsTypePmeEsi: number;
        /**
         * Nombre de clients de type Entreprises - ESI. Dont clients PE et PEES
         */
        nombreClientsTypePEetPEES: number;
        /**
         * Nombre de clients de type Entreprises - ESI. Dont clients ME et MEES
         */
        nombreClientsTypeMEetMEES: number;
        /**
         * Nombre de clients de type Entreprises - ESI. Dont clients GE et GEES
         */
        nombreClientsTypeGEetGEES: number;
        /**
         * Nombre de clients de type PRO (EI inclus).
         */
        nombreClientsTypeProEi: number;
        /**
         * Nombre de clients de type Autres (ni Entreprise - ESI, PRO, EI).
         */
        nombreClientsTypeAutre: number;
        /**
         * Nombre de rendez vous raliss pour des prospects depuis le 1er janvier de l'anne en cours.
         */
        nbRdvProspectRealiseAnneeCivile: number;
        /**
         * Nombre de prospects qui ont un RDV ralis (donc avec un CRE) depuis le 1er janvier de l'anne en cours.
         */
        nbProspectsVusAnneeEncours: number;
        /**
         * Nombre de relations co dont le leader est client (non prospect) qui ont un RDV valid (donc avec un CRE) depuis le 1er janvier de l'anne en cours.
         */
        nbRelEcoClientVuesAnneeEncours: number;
        /**
         * Nombre de rendez-vous raliss sur l'anne en cours (depuis le 1er janvier) pour
         * les relations conomiques de type "clients" (dont le leader est client) / nb de relations  conomiques de type "clients" (dont le leader est client) au jour J.
         */
        tauxDeVisite: number;
        /**
         * Le nombre total de clients qui ont une segmentation calcule dans le(s) portefeuille(s).
         */
        nombreTotalClientsSegmentation: number;
        /**
         * Nombre de rendez-vous client.
         */
        nbRdvClient: number;
        /**
         * Nombre de client vus.
         */
        nbClientsVus: number;
        /**
         * Nombre de Clients non quips
         */
        nbClientsNonEquipe: number;
        /**
         * Nombre Clients PE et PEES
         */
        nbClientsPePees: number;
        /**
         * Nombre Clients ME et MEES
         */
        nbClientsMeMees: number;
        /**
         * Nombre Clients GE et GEES
         */
        nbClientsGeGees: number;
        /**
         * Nombre de Clients avec Produit Service
         */
        nbClientsAvecProduitService: number;
        /**
         * Nombre de Clients sans contrat et prospect.
         */
        nbClientsSansContratEtProspect: number;
    }

    export interface ISegmentationRelationnellePmeEsi {
        /**
         * Valeurs : 
         * - A_ACQU : A Acqurir  
         * - A_DEVP : A Dvelopper
         * - A_FIDE  : A Fidliser 
         * - A_GERE : A Grer     
         * - A_PRES : A Prserver 
         * - A_SURV : A Surveiller
         * - NONAFF : Non concern
         */
        codeSegmentation: string;
        /**
         * Libell segmentation.
         * Valeurs : A Acqurir, A Dvelopper, A Fidliser, A Grer, A Prserver, A Surveiller, Non Concern.
         */
        libelleSegmentation: string;
        /**
         * Pourcentage nombre de clients des portefeuilles dans segmentation.
         */
        pourcentageNombreDeClients: number;
    }

    export interface IStatPortefeuille {
        portefeuilleRdv: IPortefeuilleRdv;
        listeSegmentationRelationnellePmeEsi: Array<ISegmentationRelationnellePmeEsi>;
        listeSegmentationRelationnellePro: Array<ISegmentationRelationnellePro>;
    }

    export interface ISegmentationRelationnellePro {
        /**
         * Valeurs :
         * - SCI : Socit Civile Immobilire
         * - MED* : Mdium PRO Faiblement ou non Capt
         * - MED** : Mdium PRO Partiellement Capt
         * - MED*** : Mdium PRO Fortement Capt
         * - LRG* : Large PRO Faiblement ou non Capt
         * - LRG** : Large PRO Partiellement Capt 
         * - LRG*** : Large PRO Fortement Capt
         * - MED_ER : Mdium PRO Entre en Relation
         * - MED_RK : Mdium PRO Risqu
         * - LRG_ER : Large PRO Entre en Relation
         * - RG_RK : Large PRO Risqu
         * - SML* : Small PRO Faiblement ou non Capt
         * - SML** : Small PRO Partiellement Capt
         * - SML*** : Small PRO Fortement Capt
         * - PLR_ER : PLR Entre en Relation
         * - PLR_RK : PLR Risqu
         * - SML_ER : Small PRO Entre en Relation 
         * - SML_RK : Small PRO Risqu
         * - PLR* : PLR Faiblement ou non Capt
         * - PLR** : PLR Partiellement Capt
         * - PLR*** : PLR Fortement Capt 
         * - noncalNon : Calcul 
         * - NONAFF : Non Concern
         */
        codeSegmentation: string;
        /**
         * Libell segmentation.
         * Valeurs :
         * Socit Civile Immobilire, Mdium PRO Faiblement ou non Capt, Mdium PRO Fortement Capt ...
         */
        libelleSegmentation: string;
        /**
         * Pourcentage nombre de clients des portefeuilles dans segmentation.
         */
        pourcentageNombreDeClients: number;
    }
};