declare module Portefeuille.Modeles.Rest.PortefeuilleV1.suiviComplementaire {
    export interface ISuiviComplementaire {
        listeSuivi: Array<ISuivi>;
        suiviComplementaireModifie: ISuiviComplementaireModifie;
    }

    export interface ISuiviComplementaireModifie {
        /**
         * Code Etab Banque Entit Juridique
         */
        codeEtablissement: string;
        /**
         * Identifiant personne
         */
        numeroPersonne: number;
        /**
         * Code type suivi complmentaire
         */
        codeTypeSuiviComplementaire: string;
        /**
         * Code action
         */
        codeAction: string;
        /**
         * Identifiant Elment structure
         */
        idEdsInterne: number;
        /**
         * Identifiant lment structure
         */
        identifiantElementStructure: number;
        /**
         * Rfrence externe agent
         */
        identifiantAgent: number;
    }

    export interface ISuivi {
        /**
         * code banque
         */
        codeEtablissement: string;
        /**
         * numro de la personne
         */
        numeroPersonne: number;
        /**
         * Code type :
         * '001'      MIDDLE          
         * '002'      INTERNATIONAL   
         * '003'      NEO BUSINESS    
         * '004'      SANTE           
         * '005'      GRANDS COMPTES  
         * '006'      FLUX            
         * '007'      BANQUE DE L'ORNE
         */
        codeTypeSuiviComplementaire: string;
        /**
         * Exemples de valeurs :    
         *            ---------------------    
         * '001'      MIDDLE                   
         * '002'      INTERNATIONAL            
         * '003'      NEO BUSINESS             
         * '004'      SANTE                    
         * '005'      GRANDS COMPTES           
         * '006'      FLUX                     
         * '007'      BANQUE DE L'ORNE
         */
        libelleSuiviComplementaire: string;
        /**
         * Exemples de valeurs :       
         *            ---------------------       
         * '001'      MIDDLE                      
         * '002'      Conseiller International    
         * '004'      Expert SANTE
         */
        libelleSuiviComplementClient: string;
        /**
         * identifiant de l'element de structure interne
         */
        idEdsInterne: number;
        /**
         * designation longue de l'element de structure
         */
        designationLongueEds: string;
        informationAnimation: IInformationAnimation;
        /**
         * Valeurs :            
         *            ---------            
         * 'O'        Oui  restituer      
         * 'N'        Non ne pas restituer
         */
        indicateurRestitutionClient: string;
        /**
         * Valeurs :            
         *            ---------            
         * 'O'        Oui  restituer      
         * 'N'        Non ne pas restituer
         */
        indicateurRestitutionClientRMP: string;
        dateMajEnregistrement: string;
        /**
         * exemple :
         */
        typeElementStructure: string;
    }

    export interface IInformationAnimation {
        /**
         * '1' Personne physique 
         * '2' Personne morale ou assimile morale
         */
        codePersonaliteJuridique: string;
        /**
         * exemple de formulation : "civilit pers 1" et "civilit pers2" et "dsignat p1" "dsignation pers. 1" et "dsignat pers. 2" "dsignation pers. 1" et "dsignat pers. 2" "et leurs enfants" "Groupe" 
         * raison sociale personne morale leader "Famille" nom leader pp + "& Entp" raison sociale leader pm
         */
        intituleRelation: string;
        /**
         * Numro d'identification de la relation conomique
         */
        identifiantRelationEconomique: number;
        /**
         * Nombre de personne appartenant a un mnage conomique
         */
        nombrePersonneMenage: number;
        /**
         * Correspond au 2 premiers caractres du code segmentation sur 8 car : voir CODLSH.
         * Valeurs : 'AC' Activation Clientle Particulier 'AP' Activation Clientle Professionnel 'BM' Segmentation relationelle BDR 'BR' Segmentation relation conomique BDR 'FC' Frquence Client 'FP' 
         * Frquence PRO 'FR' Frquence Relation 'MN' March national 'SL' Spcifique VAO pour progiciel Norkom / LAB 'SA' Activation Clientle V2 'SU' Univers de dtention
         */
        codeTypeSegmentClientele: string;
        /**
         * Nouveau code segmentation nationale CENCEP 1994 Ce code passe de 2  6 positions 1er caractre : le code maxi groupe J jeune, I inactif, B bancaris, M monodtenteur D diversifi, 
         * N nouveau client 2me caractre : le maxi groupe c'est un dcoupage qui intervient pour certain maxi groupe 3me au 5me caractres : codes segments comportemen- tal 6me caractre : potentiel client '+' ou ' '
         */
        codeSegmentClientele: string;
        /**
         * Libell court de la segmentation clientle pour affichage sur le poste de travail libell  caractre confidentiel pour ne pas tre interprt par la clientle
         */
        libelleCourtSegmentation: string;
        /**
         * Correspond au 2 premiers caractres du code segmentation
         */
        typeSegmentation: string;
        /**
         * Ce code passe de 2  6 positions 1er caractre : le code maxi groupe J jeune, I inactif, B bancaris, M monodtenteur D diversifi, N nouveau client 2me caractre : 
         * le maxi groupe c'est un dcoupage qui intervient pour certain maxi groupe 3me au 5me caractres : codes segments comportemen- tal 6me caractre : potentiel client '+' ou '
         */
        codeSegmentClientele2: string;
        /**
         * Libell contenant la dsignation d'un segment clientle, associ au code segment / CODLSH. Etabli dans la table de typologie DU2L
         */
        libelleSegmentClientele2: string;
        /**
         * Dtermine le rle d'une personne dans une relation Permet de dterminer si la personne est leader valeurs : '1' personne '2' personne leader d'un mnage '3' autre personne d'un mnage
         */
        typeRoleRelation: string;
        /**
         * Date du dernier rendez-vous ralis pour une personne.
         * Type date au format yyyy-MM-dd
         */
        dateDernierRdvRealise: string;
        /**
         * Nombre de rendez-vous sur 12 mois glissants
         */
        nombreRdv12Mois: number;
        /**
         * Nombre de rendez-vous planifis pour une personne
         */
        nombreRdvPlanifies: number;
        /**
         * Nombre de rendez-vous sur l'anne en cours pour une personne
         */
        nombreRdvAnneeEnCours: number;
        /**
         * Numro SIREN entreprise
         */
        numeroSiren: string;
        /**
         * Dsignation de la personne morale
         */
        raisonSociale: string;
        /**
         * Valeurs possibles : 
         * '0' Personne connue comme client 
         * '1' Personne connue comme tiers 
         * '2' Personne connue comme prospect 
         * '3' Personne connue comme prospect enrichi
         */
        codeTypeRelationPersonne: string;
        /**
         * Date du prochain rendez-vous pour une personne.
         * Type date au format yyyy-MM-dd
         */
        dateProchainRdv: string;
        /**
         * Date du dernier entretien ralis pour une personne.
         * Type date au format yyyy-MM-dd
         */
        dateDernierEntretien: string;
    }

};