declare module Portefeuille.Modeles.Rest.StructureV2.edsniveauinferieur {
    export interface IEdsniveauinferieurQuery {
        /**
         * Identifiant interne de l'EDS de rattachement de l'EDS identifi
         */
        codeEtablissement: string;
        /**
         * Rfrence externe de l'EDS de rattachement de l'EDS identifi
         */
        identifiantInterneEDS: number;
    }

    export interface IInfoRattachementEDS {
        /**
         * Identifiant interne de l'EDS de rattachement de l'EDS identifi
         */
        identifiantEDSRattachement: number;
        /**
         * Rfrence externe de l'EDS de rattachement de l'EDS identifi
         */
        referenceEDSRattachement: number;
        /**
         * Libell de l'EDS de rattachement de l'EDS identifi
         */
        libelleEDSRattachement: string;
        /**
         * Type de l'EDS de rattachement de l'EDS identifi
         */
        typeEDSRattachement: string;
        /**
         * Libell long type de l'EDS de rattachement
         */
        libelleTypeEDSRattachement: string;
    }

    export interface IInfoEDS {
        /**
         * Identifiant interne de l'lment de structure
         */
        identifiantInterneEDS: number;
        /**
         * Rfrence externe de l'lment de structure
         */
        referenceExterneEDS: number;
        /**
         * Libell de l'lment de structure
         */
        libelleEDS: string;
        /**
         * Type de l'lment structure
         */
        typeEDS: string;
        /**
         * Libell long type de l'EDS
         */
        libelleTypeEDS: string;
    }

    export interface IInfoPosteFonctionnel {
        /**
         * Identifiant interne du poste fonctionnel
         */
        identifiantPosteFonctionnel: number;
        /**
         * Rfrence externe du poste fonctionnel
         */
        referencePosteFonctionnel: number;
        /**
         * Libell du poste fonctionnel
         */
        libellePosteFonctionnel: string;
        /**
         * Type de l'lment structure
         */
        typeEDS: string;
        /**
         * Libell long type de l'EDS
         */
        libelleTypeEDS: string;
        /**
         * Rfrence externe de l'agent
         */
        codeAgent: number;
        /**
         * Dsignation longue de l'agent Elle est utilise pour la constitution de la ligne 1 de l'adresse. 
         * Elle est constitue en concatnant la civilit, le nom d'usage et le prnom de l'agent.
         */
        designationAgent: string;
        /**
         * Type de fonction de l'agent
         */
        typeFonction: string;
        /**
         * Libell de fonction de l'agent
         */
        libelleFonction: string;
        /**
         * Code civilit de l'agent
         */
        civiliteAgent: string;
        /**
         * Nom de filiation de la personne physique, correspond :
         * 
         * soit au nom du pre
         * soit au nom de la mre
         * soit aux 2 noms accols (ordre quelconque, dans la limite d'un nom de famille par parent) 
         * C'est le seul nom mentionn sur les actes d'tat civil (acte de naissance, de mariage, livret de famille....). 
         * Figure sur toute pice officielle d'identit. Appel auparavant nom patronymique.
         */
        nomFamilleAgent: string;
        /**
         * Premier prnom de l'tat civil de la personne physique.
         */
        prenomAgent: string;
        /**
         * Dnomination de la personne autre que le nom de famille, correspond :
         * 
         * soit au nom de famille de l'poux(se) (conjoint)
         * soit au nom compos de son nom de famille et de celui de l'poux(se) dans un ordre quelconque Dnomination 
         * utilisable dans la vie quotidienne, totalement facultative et sans caractre automatique. 
         * Il n'est pas possible d'utiliser comme nom d'usage le nom du concubin ou du partenaire de Pacs. 
         * C'est une information pouvant figurer sur toute pice officielle d'identit  la suite du nom de famille. 
         * Appel auparavant nom marital.
         */
        nomUsageAgent: string;
    }

    export interface IEdsNiveauInferieur {
        infoRattachementEDS: IInfoRattachementEDS;
        infoEDS: Array<IInfoEDS>;
        infoPosteFonctionnel: Array<IInfoPosteFonctionnel>;
    }

};